
<div>
  <el-table
    ref="table"
    :data="tableData"
    style="width: 100%"
    :default-expand-all="false"
    row-key="id"
    :expand-row-keys="expandRowKeys"
    @expand-change="handleExpandChange">
    <el-table-column type="expand">
      <template slot-scope="props">
        <el-form label-position="left" inline class="demo-table-expand">
          <el-form-item label="商品名称">
            <span>{{ props.row.name }}</span>
          </el-form-item>
          <el-form-item label="所属店铺">
            <span>{{ props.row.shop }}</span>
          </el-form-item>
          <el-form-item label="商品 ID">
            <span>{{ props.row.id }}</span>
          </el-form-item>
          <el-form-item label="店铺 ID">
            <span>{{ props.row.shopId }}</span>
          </el-form-item>
          <el-form-item label="商品分类">
            <span>{{ props.row.category }}</span>
          </el-form-item>
          <el-form-item label="店铺地址">
            <span>{{ props.row.address }}</span>
          </el-form-item>
          <el-form-item label="商品描述">
            <span>{{ props.row.desc }}</span>
          </el-form-item>
        </el-form>
      </template>
    </el-table-column>
    <el-table-column
      label="商品 ID"
      prop="id">
    </el-table-column>
    <el-table-column
      label="商品名称"
      prop="name">
    </el-table-column>
    <el-table-column
      label="描述"
      prop="desc">
    </el-table-column>
  </el-table>
<div style="margin-top: 20px">
  <el-button @click="toggleExpand([tableData[1], tableData[2]])">切换第二、第三行的展开收起状态</el-button>
  <el-button @click="toggleExpand(tableData, false)">全部收起</el-button>
  <el-button @click="setExpandRowKeys()">setExpandRowKeys</el-button>
  <el-button @click="insert()">insert</el-button>
</div>
</div>
